                    .span-3 延川红枣，陕西省延安市延川县特产，中国国家地理标志产品。红枣在延川栽培历史悠久，加之延川的海拔高度、光照时数、年均气温、绵沙土壤等非常适宜红枣生长。所产红枣肉厚核小，品质优良，营养价值高，特别是品种之一的狗头枣品质优异，获“后稷特别奖”，并为朝鲜领导人称赞。
                    .span-3 延川县苹果生产具有较长的历史。自古群众就有栽植果树、自给自足的传统习惯。1988年，延川县将苹果生产列为全县农村四大主导产业之一，成立了“延川县果树技术服务站”，为全县果树生产发展奠定了基础，建立了一批规范化的苹果生产示范园，科学管理技术得以推广。2005年被评为“陕西省优质苹果生产基地县”， 苹果已成为全县果农增产增收、农民致富的优势产业。
